<template>
  <div class="project" :class="{complete: item.complete}">
      <div class="actions" >
          <h3 @click="showDetails = !showDetails">{{ item.title}}</h3>
          <div class="icons">
              <router-link :to="{name: 'EditProject', params: {id: item.id}}">
                   <span class="material-icons">edit</span>
              </router-link>
              <span @click="deleteProject" class="material-icons">delete</span>
              <span @click="planComplete" class="material-icons tick">done</span>
          </div>
      </div>
      <div v-if="showDetails" class="details">
          <p>{{item.details}}</p>
    </div>
  </div>
</template>

<script>
export default {
    props:['item'],
    data(){
        return {
            showDetails: false,
            uri: "http://localhost:3000/projects/" + this.item.id
        }
    },
    methods:{
        deleteProject(){
            fetch(this.uri,{method: 'DELETE'})
            .then(() => this.$emit('delete', this.item.id))
            .catch(err => console.log(err.message))
        },
        planComplete(){
            fetch(this.uri,{
            method: 'PATCH',
            headers: {'Content-Type' : 'application/json'},
            body: JSON.stringify({complete: !this.item.complete})
            }).then(() => this.$emit('complete', this.item.id))
            .catch((err) => console.log(err.message))

        }
    }
}
</script>

<style>
.project {
    margin:20px auto;
    background: white;
    padding: 10px 20px;
    border-radius: 4px;
    box-shadow: 1px 2px 3px rgba(0,0,0,0.05);
    border-left: 4px solid #e90074;
}
h3{
    cursor: pointer;
}
.actions{
    display:flex;
    justify-content: space-between;
    align-items: center;
}
.material-icons {
    font-size: 24px;
    margin-left: 10px;
    cursor: pointer;
}
.material-icons:hover{
    color: rgb(108, 108, 224);
}
.project.complete{
    border-left: 4px solid rgb(44, 167, 44);
}
.project.complete .tick{
   color: rgb(44, 167, 44);
}

</style>