<template>
  <nav class="fitler-nav">
      <button @click="updateFilter('all')" :class="{active: current === 'all'}">View All</button>
      <button @click="updateFilter('completed')" :class="{active: current === 'completed'}">Completed</button>
      <button @click="updateFilter('ongoing')" :class="{active: current === 'ongoing'}">Ongoing</button>
  </nav>
</template>

<script>
export default {
    props:['current'],
    methods:{
        updateFilter(by){
            this.$emit('filterChange', by)
        }
    }

}
</script>

<style>
    .fitler-nav button {
        background: none;
        border:none;
        color: rgb(182, 163, 163);
        outline: none;
        font-size: 16px;
        text-transform: uppercase;
        margin-right: 10px;
        letter-spacing: 1px;
        font-weight: bold;
        cursor: pointer;
    }
    .fitler-nav button.active{
        color:#555;
    }
</style>